import { Component, TemplateRef, ElementRef } from '@angular/core';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-videopop',
  templateUrl: './videopop.component.html',
  styleUrls: ['./videopop.component.css']
})
export class VideopopComponent {
  modalRef: BsModalRef;
  index: number;
  vidSrc: string[] = ['https://www.youtube.com/embed/bILE5BEyhdo',
    'https://www.youtube.com/embed/TcMBFSGVi1c',
    'https://www.youtube.com/embed/hNCmb-4oXJA'];
  constructor(private modalService: BsModalService,
    private el: ElementRef,
    public sanitizer: DomSanitizer) {}



  openModal(template: TemplateRef<any>) {
    this.index = this.el.nativeElement.id;
    this.modalRef = this.modalService.show(template);
  }
}
