import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { MovieserviceService } from '../services/movieservice.service';
import { Movie } from '../models/movieModel';

@Component({
  selector: 'app-seriespage',
  templateUrl: './seriespage.component.html',
  styleUrls: ['./seriespage.component.css']
})

export class SeriespageComponent implements OnInit {

  movie: Movie;
  cat: string[];

  constructor(private route: ActivatedRoute, private service: MovieserviceService) { }

  ngOnInit() {
    this.route.params.subscribe((params) => {
      const id = params['id'];
      console.log(id);
      this.service.getMovies(id).subscribe((data) => {
        console.log(data);
        this.movie = data;
        this.cat = data.Genre.split(',');

      });
    });
  }
}
